/* BARRA DE NAVEGACIÃ“N */
.navigation {
    position: fixed;
    min-height: 100vh;
    width: 70px;
    background: hsl(248, 16%, 27%);
    box-shadow: 5px 0 0 #0EA2F6;
    overflow: hidden;
    transition: width 0.5s;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; /* Para colocar los elementos debajo del otro */
    align-items: center;
}

.navigation:hover {
    width: 300px;
    transition: none;
}

.navigation ul {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    padding-left: 5%;
}

.navigation ul li {
    position: relative;
    list-style: none;
    width: 100%;
}

.navigation ul li:hover {
    background: #0EA2F6;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
}

.navigation ul li button {
    position: relative;
    width: 100%;
    display: flex;
    text-decoration: none;
    background: transparent;
    border: none;
    color: #F4FCFE;
    font-family: "Poppins-Bold";
    font-size: 12pt;
}

.navigation ul li button .icon {
    position: relative;
    display: block;
    min-width: 60px;
    height: 60px;
    line-height: 75px;
    padding: 10px
}

.navigation ul li button .title {
    position: relative;
    display: block;
    padding-left: 10px;
    height: 60px;
    line-height: 60px;
    white-space: nowrap;
}